import React, { useState, useEffect, useRef } from 'react';
import TypewriterText from './TypewriterText';
import { motion } from 'framer-motion';
import Prism from 'prismjs';

interface TypewriterCodeProps {
  code: string;
  language: string;
  speed?: number;
  lineDelay?: number;
  className?: string;
  onComplete?: () => void;
  showThinking?: boolean;
}

const TypewriterCode: React.FC<TypewriterCodeProps> = ({
  code,
  language,
  speed = 12,
  lineDelay = 200,
  className = '',
  onComplete,
  showThinking = true
}) => {
  const [currentLine, setCurrentLine] = useState(0);
  const [isThinking, setIsThinking] = useState(showThinking);
  const [isComplete, setIsComplete] = useState(false);
  const [autoSwitched, setAutoSwitched] = useState(false);
  const codeRef = useRef<HTMLElement>(null);
  
  // Split code into lines, preserving empty lines
  const lines = code.split('\n');

  useEffect(() => {
    if (showThinking) {
      // Show thinking animation for 0.8 seconds
      const thinkingTimer = setTimeout(() => {
        setIsThinking(false);
      }, 800);
      return () => clearTimeout(thinkingTimer);
    }
  }, [showThinking]);

  // Auto-switch to syntax highlighting after completion
  useEffect(() => {
    if (isComplete && !autoSwitched) {
      const switchTimer = setTimeout(() => {
        setAutoSwitched(true);
        if (codeRef.current) {
          Prism.highlightElement(codeRef.current);
        }
      }, 1000); // Wait 1 second after completion
      return () => clearTimeout(switchTimer);
    }
  }, [isComplete, autoSwitched]);

  const handleLineComplete = () => {
    if (currentLine < lines.length - 1) {
      setTimeout(() => {
        setCurrentLine(prev => prev + 1);
      }, lineDelay);
    } else {
      setIsComplete(true);
      onComplete?.();
    }
  };

  // Function to highlight a single line
  const highlightLine = (line: string, language: string) => {
    try {
      const highlighted = Prism.highlight(line, Prism.languages[language] || Prism.languages.javascript, language);
      return { __html: highlighted };
    } catch (error) {
      return { __html: line };
    }
  };

  if (isThinking) {
    return (
      <div className={`${className} flex items-center space-x-2 p-4 bg-gray-50 dark:bg-gray-800 border border-gray-200 dark:border-gray-700 rounded-lg`}>
        <span className="text-gray-600 dark:text-gray-300">Generating code</span>
        <div className="flex space-x-1">
          <motion.div
            className="w-1.5 h-1.5 bg-blue-500 dark:bg-blue-400 rounded-full"
            animate={{ scale: [1, 1.2, 1] }}
            transition={{ duration: 0.6, repeat: Infinity, delay: 0 }}
          />
          <motion.div
            className="w-1.5 h-1.5 bg-blue-500 dark:bg-blue-400 rounded-full"
            animate={{ scale: [1, 1.2, 1] }}
            transition={{ duration: 0.6, repeat: Infinity, delay: 0.1 }}
          />
          <motion.div
            className="w-1.5 h-1.5 bg-blue-500 dark:bg-blue-400 rounded-full"
            animate={{ scale: [1, 1.2, 1] }}
            transition={{ duration: 0.6, repeat: Infinity, delay: 0.2 }}
          />
        </div>
      </div>
    );
  }

  return (
    <div className={`${className} bg-white dark:bg-gray-800 border border-gray-200 dark:border-gray-700 rounded-lg overflow-hidden shadow-sm`}>
      <div className="flex items-center justify-between px-4 py-2 bg-gray-50 dark:bg-gray-700 border-b border-gray-200 dark:border-gray-600">
        <div className="flex items-center space-x-2">
          <div className="flex space-x-1">
            <div className="w-3 h-3 bg-red-500 rounded-full"></div>
            <div className="w-3 h-3 bg-yellow-500 rounded-full"></div>
            <div className="w-3 h-3 bg-green-500 rounded-full"></div>
          </div>
          <span className="text-gray-600 dark:text-gray-300 text-sm font-medium">{language}</span>
          {autoSwitched && (
            <span className="text-xs bg-green-100 dark:bg-green-900 text-green-700 dark:text-green-300 px-2 py-1 rounded">
              Syntax Highlighted
            </span>
          )}
        </div>
      </div>
      
      <div className="p-4 font-mono text-sm bg-gray-50 dark:bg-gray-900">
        {autoSwitched ? (
          // Show full syntax-highlighted code after auto-switch
          <pre className="overflow-x-auto">
            <code
              ref={codeRef}
              className={`language-${language}`}
            >
              {code}
            </code>
          </pre>
        ) : (
          // Show typewriter effect with real-time syntax highlighting
          lines.map((line, index) => (
            <div key={index} className="leading-relaxed min-h-[1.5rem]">
              {index < currentLine && (
                <pre 
                  className="text-gray-800 dark:text-gray-200 whitespace-pre-wrap"
                  dangerouslySetInnerHTML={highlightLine(line, language)}
                />
              )}
              {index === currentLine && (
                <div className="text-gray-800 dark:text-gray-200 whitespace-pre-wrap">
                  <TypewriterText
                    text={line}
                    speed={speed}
                    onComplete={handleLineComplete}
                    cursor={index === lines.length - 1 && !isComplete}
                  />
                </div>
              )}
            </div>
          ))
        )}
      </div>
    </div>
  );
};

export default TypewriterCode; 