import React from 'react';
import { CodeExample as CodeExampleType } from '../types';
import { motion } from 'framer-motion';
import MultiLanguageCode from './MultiLanguageCode';
import LegacyCodeExample from './LegacyCodeExample';

interface CodeExampleProps {
  example: CodeExampleType;
}

const CodeExample: React.FC<CodeExampleProps> = ({ example }) => {
  // Check if this is legacy format (has 'language' and 'code' properties)
  const isLegacyFormat = 'language' in example && 'code' in example;
  
  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.3 }}
      className="bg-white dark:bg-gray-800 border border-gray-200 dark:border-gray-700 rounded-lg overflow-hidden shadow-sm p-6"
    >
      {isLegacyFormat ? (
        <LegacyCodeExample 
          example={example as any}
          showThinking={true}
        />
      ) : (
        <MultiLanguageCode 
          codeExample={example as any}
          showThinking={true}
        />
      )}
    </motion.div>
  );
};

export default CodeExample; 