import React, { useState } from 'react';
import TypewriterCode from './TypewriterCode';

import 'prismjs/components/prism-java';
import 'prismjs/components/prism-python';
import 'prismjs/components/prism-go';
import 'prismjs/components/prism-cpp';

// Type guard to check if it's the new format
interface NewFormatCodeExample {
  id: string;
  title: string;
  explanation: string;
  implementations: {
    java: { code: string; highlightLines?: number[] };
    javascript: { code: string; highlightLines?: number[] };
    python: { code: string; highlightLines?: number[] };
    golang: { code: string; highlightLines?: number[] };
    cpp: { code: string; highlightLines?: number[] };
  };
}

interface MultiLanguageCodeProps {
  codeExample: NewFormatCodeExample;
  className?: string;
  onComplete?: () => void;
  showThinking?: boolean;
}

type Language = 'java' | 'javascript' | 'python' | 'golang' | 'cpp';

const languageInfo = {
  java: { name: 'Java', extension: 'java', prismLang: 'java' },
  javascript: { name: 'JavaScript', extension: 'js', prismLang: 'javascript' },
  python: { name: 'Python', extension: 'py', prismLang: 'python' },
  golang: { name: 'Go', extension: 'go', prismLang: 'go' },
  cpp: { name: 'C++', extension: 'cpp', prismLang: 'cpp' }
};

const MultiLanguageCode: React.FC<MultiLanguageCodeProps> = ({
  codeExample,
  className = '',
  onComplete,
  showThinking = true
}) => {
  const [activeLanguage, setActiveLanguage] = useState<Language>('java'); // Default to Java

  const handleLanguageChange = (language: Language) => {
    setActiveLanguage(language);
  };

  return (
    <div className={className}>
      {/* Language Tabs */}
      <div className="mb-4">
        <div className="border-b border-gray-200 dark:border-gray-700">
          <nav className="-mb-px flex space-x-8 overflow-x-auto">
            {Object.entries(languageInfo).map(([key, info]) => {
              const language = key as Language;
              const isActive = activeLanguage === language;
              return (
                <button
                  key={language}
                  onClick={() => handleLanguageChange(language)}
                  className={`py-2 px-1 border-b-2 font-medium text-sm whitespace-nowrap transition-colors ${
                    isActive
                      ? 'border-blue-500 text-blue-600 dark:text-blue-400 dark:border-blue-400'
                      : 'border-transparent text-gray-500 hover:text-gray-700 dark:text-gray-400 dark:hover:text-gray-300 hover:border-gray-300 dark:hover:border-gray-600'
                  }`}
                >
                  {info.name}
                </button>
              );
            })}
          </nav>
        </div>
      </div>

      {/* Code Content */}
      <div>
        <h4 className="text-lg font-semibold text-gray-900 dark:text-white mb-3">
          {codeExample.title}
        </h4>
        
        <TypewriterCode
          code={codeExample.implementations[activeLanguage].code}
          language={languageInfo[activeLanguage].prismLang}
          speed={3}
          lineDelay={80}
          onComplete={onComplete}
          showThinking={showThinking}
          className="mb-4"
        />
        
        <div className="mt-4 p-4 bg-blue-50 dark:bg-blue-900/20 border border-blue-200 dark:border-blue-800 rounded-lg">
          <p className="text-sm text-blue-800 dark:text-blue-200">
            <strong>Explanation:</strong> {codeExample.explanation}
          </p>
        </div>
      </div>
    </div>
  );
};

export default MultiLanguageCode; 